.leaderboard {
  width: -webkit-fill-available;
  background-image: url("../../assets/images/leader.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;

  padding: 2rem 3rem;

  .content-wrapper {
    display: flex;
    flex-direction: column;
    flex: 1;
    height: 90%;
    overflow: hidden;

    max-width: 700px;
  }

  .row_position {
    display: flex;
    width: 30px;
    height: 22px;
    border: 2px solid #ffffff;
    border-radius: 16px;
    align-items: center;
    justify-content: center;
  }
  .row_lvl {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 22px;
    width: 47px;
    background: white;
    border-radius: 16px;
    color: black;
  }

  .row_player {
    display: flex;
    gap: 12px;

    & > div {
      display: flex;
      flex-direction: column;
      gap: 4px;
      .row_name {
        font-weight: bold;
      }
    }
  }

  /**
  * NEW
  */

  .modal_table {
    display: flex;
    flex: 1;

    border-left: 2px solid white;
    border-right: 2px solid white;
    border-bottom: 2px solid white;
    border-bottom-right-radius: 12px;
    border-bottom-left-radius: 12px;

    background: rgba(0, 0, 0, 0.7);
    overflow-y: scroll;
    & * {
      color: white;
    }
  }

  table {
    .table_row {
      cursor: pointer;
      height: 2rem;

      &:hover {
        background: rgba(255, 255, 255, 0.1);
      }

      &.table_row_highlighted {
        background: rgba(135, 147, 255, 0.228);
      }
    }

    .badge {
      display: inline-flex;
      align-items: center;
      justify-content: center;
      //
      border: 2px solid #ffffff;
      border-radius: 16px;
      //
      font-size: 14px;
      font-weight: bold;
      //
      padding: 0.2rem 1rem;
    }
  }
}

.row_ownerId {
  border-bottom: 1px solid #35b5d4;
  color: #35b5d4;
}
